<template>
  <div>
    <div style="display: flex;justify-content: space-between;">
      <div>商户信息列表</div>
      <el-button @click="newlyAdded.dialogVisible = true" type="primary" icon="el-icon-search">新增商户</el-button>
    </div>
    <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="adoptApproved.tableData"
      tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
      :header-cell-style="headerStyle">
      <el-table-column type="index" label="序号" width="50">
      </el-table-column>
      <el-table-column v-for="(item, index) in adoptApproved.tableMsg" :key="index" :prop="item.key" :label="item.name">
      </el-table-column>
      <el-table-column label="优惠券推广">
        <template slot-scope="scope">
          <i v-if="scope.row.bank_mer_permissions.indexOf('1')>-1" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </template>
      </el-table-column>
      <el-table-column label="商品推广">
        <template slot-scope="scope">
          <i v-if="scope.row.bank_mer_permissions.indexOf('2')>-1" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </template>
      </el-table-column>

      <el-table-column fixed="right" label="操作" width="400">
        <template slot-scope="scope">
          <el-button @click="PopupControl(scope,functionMsg,'功能开通')" type="text" size="small">功能开通</el-button>
          <el-button @click="PopupControl(scope,serviceChargeMsg,'服务费管理')" type="text" size="small">服务费管理</el-button>
          <el-button @click="PopupControl(scope,merchantInformation,'商户信息')" type="text" size="small">商户信息</el-button>
          <el-button @click="PopupControl(scope,franchiseOutlets,'加盟网点查看')" type="text" size="small">已入驻的外部网点</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--page start 分页  current-page:当前页，handleSizeChange：每页显示条数改变的时候，page-size：每页显示条数，total：总条数，handleCurrentChange：下一页-->
    <el-pagination style="margin-top: 10px;" background @size-change="adoptApprovedSizeChange"
      @current-change="adoptApprovedCurrentChange" :current-page="adoptApproved.currentPage"
      :page-sizes="[10, 20, 50, 100,200]" :page-size="adoptApproved.perPageSize"
      layout="total, sizes, prev, pager, next, jumper" :total="adoptApproved.total">
    </el-pagination>
    <!-- 功能开通弹框 -->
    <el-dialog title="功能开通" :visible.sync="functionMsg.dialogVisible" width="20%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{functionMsg.name}}</div>
        <div style="margin-bottom: 10px;display: flex;" v-for="(item, index) in functionMsg.msg" :key="index">
          <div style="width: 100px;text-align: left;">{{item.name}}：</div>
          <div>
            <el-radio v-model="item.radio" label="1">开通</el-radio>
            </el-radio>
            <el-radio v-model="item.radio" label="2">关闭</el-radio>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="functionMsg.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="functionClick()">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 服务费管理弹框 -->
    <el-dialog title="服务费管理" :visible.sync="serviceChargeMsg.dialogVisible" width="80%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{serviceChargeMsg.name}}</div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">收款账号：{{serviceChargeMsg.obj.mbmi_mid}}</div>
        <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="addQueryList"
          tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
          :header-cell-style="headerStyle">
          <el-table-column type="index" label="序号" width="80">
          </el-table-column>
          <el-table-column prop="di_uuid" label="设备编号">
            <template slot-scope="scope">
              <span>{{scope.row.di_uuid| format}}</span>
            </template>
          </el-table-column>
          <!-- <el-table-column v-for="(item, index) in serviceChargeMsg.msg" :key="index" :label="item.name">
          </el-table-column> -->
          <el-table-column label="平台服务费">
            <template slot-scope="scope">
              <div>每笔交易额
                <el-input style="width: 150px;" disabled :value="scope.row.mbpb_fee" size="mini"
                  placeholder="请输入百分比交易额"></el-input>%
              </div>
            </template>
          </el-table-column>
          <el-table-column label="银行服务费">
            <template slot-scope="scope">
              <div>每笔交易额
                <el-input style="width: 150px;" :disabled="scope.row.bmp_uuid&&scope.row.mbpb_fee?false:true"
                  size="mini" v-model="scope.row.bank_fee" @input='inputChange(scope.row,"bank_fee")'
                  placeholder="请输入百分比交易额">
                </el-input>%
              </div>
            </template>
          </el-table-column>
          <el-table-column label="渠道服务费">
            <template slot-scope="scope">
              <div>渠道商名称：{{scope.row.bmp_name||""}}</div>
              <div>每笔交易额
                <el-input style="width: 150px;" disabled size="mini" v-model="scope.row.mbmp_fee"
                  placeholder="请输入百分比交易额">
                </el-input>%
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="serviceChargeMsg.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="Select_shop(serviceChargeMsg.obj.mbmi_uuid,serviceChargeMsg.obj.mbmi_mid)">确 定
        </el-button>
      </span>
    </el-dialog>
    <!-- 商户信息弹框 -->
    <el-dialog title="商户信息" :visible.sync="merchantInformation.dialogVisible" width="35%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户名称：{{merchantInformation.dataMsg.bms_name}}
        </div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户简称：
          {{merchantInformation.dataMsg.bmi_mer_alias}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">统一社会信用代码：
          {{merchantInformation.dataMsg.bmi_license_code}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">所属行业： {{merchantInformation.obj.bii_name}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">每周营业日：
          {{merchantInformation.dataMsg.bms_work_d_name?JSON.parse(merchantInformation.dataMsg.bms_work_d_name)[0]+'-'+JSON.parse(merchantInformation.dataMsg.bms_work_d_name).slice(-1):""}}
        </div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">营业时间段：
          {{merchantInformation.dataMsg.bms_work_t_name}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">所在区域：
          {{merchantInformation.dataMsg.bms_area?merchantInformation.dataMsg.bms_area.replaceAll('"',"").replaceAll(',',"-"):""}}
        </div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">详细地址：
          {{merchantInformation.dataMsg.bms_address}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户登录名(手机号)：
          {{merchantInformation.dataMsg.bmi_phone}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">银联子商户号： {{merchantInformation.dataMsg.mbmi_mid}}
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="merchantInformation.dialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 加盟网点弹框 -->
    <el-dialog title="已入驻的外部网点" :visible.sync="franchiseOutlets.dialogVisible" width="80%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{franchiseOutlets.name}}</div>
        <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable"
          :data="franchiseOutlets.setData" tooltip-effect="dark" :row-style="{'height':'10px'}"
          :cell-style="{'padding':'5px 0px'}" :header-cell-style="headerStyle">
          <el-table-column type="index" label="序号" width="80">
          </el-table-column>
          <el-table-column v-for="(item, indexs) in franchiseOutlets.msg" :key="indexs" :prop="item.key"
            :label="item.name">
          </el-table-column>
        </el-table>
        <!--page start 分页  current-page:当前页，handleSizeChange：每页显示条数改变的时候，page-size：每页显示条数，total：总条数，handleCurrentChange：下一页-->
        <el-pagination style="margin-top: 10px;" background @size-change="franchiseOutletsSizeChange"
          @current-change="franchiseOutletsCurrentChange" :current-page="franchiseOutlets.currentPage"
          :page-sizes="[10, 20, 50, 100,200]" :page-size="franchiseOutlets.perPageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="franchiseOutlets.total">
        </el-pagination>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="franchiseOutlets.dialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 新增商户弹框 -->
    <el-dialog :show-close="false" :close-on-press-escape="false" :close-on-click-modal="false"
      :visible.sync="newlyAdded.dialogVisible" width="80%">
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ '新增已审核商户' }}</span>
      </div>
      <div class="box test-5">
        <div slot="title" style="text-align: left;font-size:18px;font-weight: bold;margin-left: 10px;">
          <span>基本信息</span>
        </div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="demo-ruleForm">
          <el-row :gutter="24" style="margin-bottom: 10px">
            <el-col :md="12">
              <el-form-item label="商户名称:" prop="bmi_mer_name">
                <el-input :maxlength="20" placeholder="请输入商户名称" v-model="ruleForm.bmi_mer_name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="商户简称:" prop="bmi_mer_alias">
                <el-input :maxlength="6" placeholder="请输入商户简称" v-model="ruleForm.bmi_mer_alias"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 10px">
            <el-col :md="12">
              <el-form-item label="统一社会信用代码:" prop="bms_license_code">
                <el-input :maxlength="18" placeholder="请输入统一社会信用代码(18位)" v-model="ruleForm.bms_license_code"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="商户所属行业:" prop="bmi_industry_id">
                <el-select style="width:100%" clearable placeholder="请选择" v-model="ruleForm.bmi_industry_id">
                  <el-option v-for="item in industrySelectionList" :key="item.bii_name" :label="item.bii_name"
                    :value="item.bii_id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 10px">
            <el-col :md="12">
              <el-form-item label="每周营业时间:">
                <div style="width: 100%;text-align: left;">
                  周一至周日
                </div>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="选择营业时间:">
                <div style="display: flex;">
                  <el-form-item prop="bms_work_t1">
                    <el-time-select placeholder="起始时间" v-model="ruleForm.bms_work_t1"
                      :picker-options="{start: '00:00',step: '00:30',end: '23:59'}">
                    </el-time-select>
                  </el-form-item>
                  &#x3000;-&#x3000;
                  <el-form-item prop="bms_work_t2">
                    <el-time-select placeholder="结束时间" v-model="ruleForm.bms_work_t2"
                      :picker-options="{start: '00:00',step: '00:30',end: '23:59',minTime: ruleForm.bms_work_t1}">
                    </el-time-select>
                  </el-form-item>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 10px">
            <el-col :md="12">
              <el-form-item label="所在地区:" prop="selectedOptions">
                <el-cascader style="width: 100%" :options="regionData" v-model="ruleForm.selectedOptions"
                  @change="selectPCAChange">
                </el-cascader>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="详细地址:" prop="bms_address">
                <el-input type="textarea" autosize :maxlength="30" placeholder="请输入详细地址"
                  v-model="ruleForm.bms_address">
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 10px">
            <el-col :md="12">
              <el-form-item label="商户登录名(手机号):" prop="bmi_login_id">
                <el-input :maxlength="11" placeholder="请输入商户登录名(手机号)" v-model="ruleForm.bmi_login_id"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="商户初始密码:" prop="bmi_password">
                <el-input placeholder="请输入商户初始密码" v-model="ruleForm.bmi_password"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 10px">
            <el-col :md="12">
              <el-form-item label="银联子商户号:" prop="mbmi_mid">
                <el-input placeholder="请输入银联子商户号" v-model="ruleForm.mbmi_mid"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <div style="text-align: left;font-size:18px;font-weight: bold;margin: 10px;">
            <span>功能开通</span>
          </div>
          <div style="width: 100%;text-align: left;margin:0 0 10px 10px;display: flex;"
            v-for="(item, index) in newlyAdded.msg" :key="index">
            <div style="width: 100px;text-align: left;">{{item.name}}：</div>
            <div>
              <el-radio v-model="item.radio" label="1">开通</el-radio>
              </el-radio>
              <el-radio v-model="item.radio" label="2">关闭</el-radio>
            </div>
          </div>
          <div style="text-align: left;font-size:18px;font-weight: bold;margin: 10px;">
            <span>商品推广服务费设置</span>
          </div>
          <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="addQueryList"
            tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
            :header-cell-style="headerStyle">
            <el-table-column type="index" label="序号" width="80">
            </el-table-column>
            <el-table-column prop="di_uuid" label="设备编号">
              <template slot-scope="scope">
                <span>{{scope.row.di_uuid| format}}</span>
              </template>
            </el-table-column>
            <el-table-column label="平台服务费">
              <template slot-scope="scope">
                <div>每笔交易额
                  <el-input style="width: 150px;" disabled :value="scope.row.mbpb_fee" size="mini"
                    placeholder="请输入百分比交易额"></el-input>%
                </div>
              </template>
            </el-table-column>
            <el-table-column label="银行服务费">
              <template slot-scope="scope">
                <div>每笔交易额
                  <el-input style="width: 150px;" :disabled="scope.row.bmp_uuid&&scope.row.mbpb_fee?false:true"
                    size="mini" v-model="scope.row.bank_fee" @input='inputChange(scope.row,"bank_fee")'
                    placeholder="请输入百分比交易额">
                  </el-input>%
                </div>
              </template>
            </el-table-column>
            <el-table-column label="渠道服务费">
              <template slot-scope="scope">
                <div>渠道商名称：{{scope.row.bmp_name||""}}</div>
                <div>每笔交易额
                  <el-input style="width: 150px;" disabled size="mini" v-model="scope.row.mbmp_fee"
                    placeholder="请输入百分比交易额">
                  </el-input>%
                </div>
              </template>
            </el-table-column>
          </el-table>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="resetForm('ruleForm')">关 闭</el-button>
        <el-button type="primary" @click="submitData('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
  import { regionData, CodeToText } from "element-china-area-data";
  export default {
    data() {
      return {
        indexOBJ: 0,
        pca: {
          province: '',//省
          city: '',//市
          area: '',//县
        },
        regionData: regionData,
        industrySelectionList: [],
        //主页面列表查询
        adoptApproved: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          tableMsg: [
            { key: 'mbmi_mer_name', name: '商户名称' },
            { key: 'bii_name', name: '所属行业' }
          ],
          tableData: []
        },
        //新增弹窗数据校验
        ruleForm: {
          loc: "",//地址
          bmi_mer_name: '',//商户名称
          bmi_mer_alias: '',//商户简称
          bms_license_code: '',//社会信用代码
          bmi_industry_id: '',//商户所属行业
          bms_work_t1: '',//开始营业时间
          bms_work_t2: '',//结束营业时间
          bms_address: '',//详细地址
          mbmi_mid: "",//银联子商户号
          bmi_login_id: "",//商户登录名
          bmi_password: '123456',//商户初始密码
          selectedOptions: [],//所在地区
        },
        //表单校验
        rules: {
          bmi_mer_name: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '商户名称不可输入空格、特殊符号', trigger: 'blur' },
            { required: true, message: '请输入商户名称', trigger: 'blur' },
          ],
          bmi_login_id: [
            { pattern:/^[0-9]{11}$/, message: '请输入手机号码', trigger: 'blur' },
            { required: true, message: '请输入商户登录名', trigger: 'blur' },
            { min: 11, max: 11, message: '长度在 11 个字符', trigger: 'blur' }
          ],
          bmi_password: [
            { required: true, message: '请输入商户初始密码', trigger: 'blur' },
            { min: 6, max: 18, message: '长度在 6到18 个字符', trigger: 'blur' }
          ],
          bmi_mer_alias: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '商户简称不可输入空格、特殊符号', trigger: 'blur' },
            { required: true, message: '请输入商户简称', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          bms_license_code: [
            { pattern:/^[A-Za-z0-9]{18}$/, message: '请输入统一社会信用编码（18位）', trigger: 'blur' },
            { required: true, message: '请输入社会信用编码', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          bms_address: [
            { required: true, message: '请输入详细地址', trigger: 'blur' },
            { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' }
          ],
          mbmi_mid: [
            { pattern:/^[A-Za-z0-9]{15}$/, message: '请输入银联子商户号（15位）', trigger: 'blur' },
            { required: true, message: '请输入银联子商户号', trigger: 'blur' },
            // { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' }
          ],
          bms_work_t1: [
            { required: true, message: '请选择开始营业时间', trigger: 'change' }
          ],
          bms_work_t2: [
            { required: true, message: '请选择结束营业时间', trigger: 'change' }
          ],
          bmi_industry_id: [
            { required: true, message: '请选择商户行业', trigger: 'change' }
          ],
          selectedOptions: [
            { type: 'array', required: true, message: '请选择所在地区', trigger: 'change' }
          ],
        },
        // 表格菜单
        tableMsg: [
          { key: 'date', name: '商户名称' },
          { key: 'date', name: '所属行业' },
          { key: 'date', name: '满减券推广' },
          { key: 'date', name: '抽奖优惠券' },
          { key: 'date', name: '商品推广' },
          { key: 'date', name: '代金券推广' }
        ],
        // 功能管理
        functionMsg: {
          name: "xxxxx",
          dialogVisible: false,
          msg: [
            { radio: '1', name: '满减券推广' },
            // { radio: '1', name: '抽奖优惠券' },
            { radio: '1', name: '商品推广' },
            // { radio: '1', name: '代金券推广' }
          ]
        },
        // 服务费管理
        serviceChargeMsg: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          obj: [],
