        console.log('res==', id,'---------------------------------------------------------');
        let that = this;
        let _jsondata_ = {
          obj_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: this.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: this.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: this.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.each_device_rate_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.addQueryList = res.data.rows;
            this.queryClick(id, that.addQueryList);
            loading.close();
          } else {
            that.addQueryList = [];
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.addQueryList = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.addQueryList = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.newlyAdded.dialogVisible = false
      },
      /**
       * 提交新增商户信息
      */
      submitData(formName) {
        let show = this.addQueryList.every(res => {
          console.log(res.bank_fee * 1 , res.mbmp_fee * 1 , res.mbpb_fee * 1);
          let bank_fee=res.bank_fee?res.bank_fee:0
          let mbmp_fee=res.mbmp_fee?res.mbmp_fee:0
          let mbpb_fee=res.mbpb_fee?res.mbpb_fee:0
          let number = bank_fee * 1 + mbmp_fee * 1 + mbpb_fee * 1
          return number < 30 || number == 30
        })

        if (!show) {
          this.$alert('平台服务费+银行服务费+渠道商服务费之和不可超过每笔交易额的30%', '处理失败', {
            confirmButtonText: '确定',
            callback: action => { }
          });
          return
        }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.submitForm()
          } else {
            console.log('error submit!!');
            this.$message({
              message: '请确认信息是否填写完整！',
              type: 'warning'
            });
            return false;
          }
        });
      },
      submitForm() {
        /**
       *  bmi_mer_name: '',//商户名称
       *  bmi_mer_alias: '',//商户简称
       *  bmi_login_id: "",//商户登录名
       *  bmi_password: '',//商户初始密码
       *  bmi_industry_id: '',//商户所属行业
       *  bms_license_code: '',//社会信用代码
       *  bms_work_t1: '',//开始营业时间
       *  bms_work_t2: '',//结束营业时间
       *  bms_address: '',//详细地址
       *  mbmi_mid: "",//银联子商户号
      */
        this.$confirm('在此提交的信息一经录入不可更改, 是否提交?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let that = this;
          var shop_work_t1 = that.ruleForm.bms_work_t1.split(":");
          var shop_work_t2 = that.ruleForm.bms_work_t2.split(":");
          var shop_work_t3 = '';
          if (shop_work_t1[0].indexOf("0", 0) === 0) {
            shop_work_t3 = shop_work_t1[0].substr(1, 1);
          } else {
            shop_work_t3 = shop_work_t1[0];
          }

          var shop_work_t4 = '';
          if (shop_work_t1[1].indexOf("0", 0) === 0) {
            shop_work_t4 = shop_work_t1[1].substr(1, 1);
          } else {
            shop_work_t4 = shop_work_t1[1];
          }
          var shop_work_t5 = '';
          if (shop_work_t2[0].indexOf("0", 0) === 0) {
            shop_work_t5 = shop_work_t2[0].substr(1, 1);
          } else {
            shop_work_t5 = shop_work_t2[0];
          }
          var shop_work_t6 = '';
          if (shop_work_t2[1].indexOf("0", 0) === 0) {
            shop_work_t6 = shop_work_t2[1].substr(1, 1);
          } else {
            shop_work_t6 = shop_work_t2[1];
          }
          let permissions = []
          if (this.newlyAdded.msg[0].radio == 1) {
            permissions.push(1)
          }
          if (this.newlyAdded.msg[1].radio == 1) {
            permissions.push(2)
          }
          console.log(permissions, "------------------");
          let _jsondata_ = {
            bmi_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
            bmi_province_id: that.customGlobal.userInfoExt.cui_province_id,
            bmi_city_id: that.customGlobal.userInfoExt.cui_city_id,
            bmi_cui_id: that.customGlobal.userInfoExt.bmi_cui_id,
            bmi_county_id: that.customGlobal.userInfoExt.cui_county_id,
            bmi_station_id: that.customGlobal.userInfoExt.cui_station_id,
            bcf_cui_uuid: that.customGlobal.userInfoExt.cui_uuid,
            bmi_mer_name: that.ruleForm.bmi_mer_name,
            bmi_mer_alias: that.ruleForm.bmi_mer_alias,
            bmi_login_id: that.ruleForm.bmi_login_id,
            bmi_phone: that.ruleForm.bmi_login_id,
            mbmi_mid: that.ruleForm.mbmi_mid,
            bmi_password: this.customGlobal.md5(that.ruleForm.bmi_password),
            bmi_industry_id: that.ruleForm.bmi_industry_id,
            bms_industry_id: that.ruleForm.bmi_industry_id,
            bms_area_name: that.ruleForm.selectedOptions,
            bms_address: that.ruleForm.bms_address,
            bms_license_code: that.ruleForm.bms_license_code,
            permissions: permissions,
            bms_work_t: [shop_work_t3 + ',' + shop_work_t4 + ',' + shop_work_t5 + ',' + shop_work_t6],
            bms_work_d: [0, 6],
            bms_work_t_name: that.ruleForm.bms_work_t1 + '-' + that.ruleForm.bms_work_t2,
            bms_work_d_name: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
            bms_area: that.ruleForm.loc,
            bms_name: that.ruleForm.bmi_mer_name,
            bms_phone: that.ruleForm.bmi_login_id,
            bcf_manager_id: "",
            bcf_manager_name: "",
            mbmi_contacts: "",//联系人
            mbmi_phone: that.ruleForm.bmi_login_id,
          };
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',

            background: 'rgba(0, 0, 0, 0)'
          });
          var a1 = this.customGlobal.postHttpClient(
            this.customGlobal.serverUrl.shop_add,
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            { "Content-Type": "application/x-www-form-urlencoded" },
            this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
          );
          a1.then((res) => {
            console.log("异步调用成功返回:" + JSON.stringify(res));
            if (res.data.status == 'success') {
              loading.close();
              this.Select_shop(res.data.mer_uuid, this.ruleForm.mbmi_mid, true);
            } else {
              console.info("商户新增失败");
              loading.close();
              this.newlyAdded.dialogVisible = false
              this.serviceChargeMsg.dialogVisible = false
              this.resetForm('ruleForm')
              this.getData()
              this.$alert(res.data.msg, '商户新增失败', {
                confirmButtonText: '确定',
                callback: action => {
                  console.info('data:' + JSON.stringify(res.data));
                  loading.close();
                }
              });
              if (res.data.msg === '请登录认证') {
                this.$router.push({ path: '/login' });
              }
            }
          }, error => {
            console.log("异步调用出错:" + JSON.stringify(error));
            loading.close();
            this.newlyAdded.dialogVisible = false
            this.serviceChargeMsg.dialogVisible = false
            this.resetForm('ruleForm')
            this.getData()
            // this.companyDialogFormVisible =false;//保存失败关闭dialog
            this.$alert(JSON.stringify(error.message), '商户新增失败!', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          }).catch((res) => {
            console.log("异步调用失败返回:" + JSON.stringify(res));
            loading.close();
            this.newlyAdded.dialogVisible = false
            this.serviceChargeMsg.dialogVisible = false
            this.resetForm('ruleForm')
            this.getData()
            // this.companyDialogFormVisible =false;//保存失败关闭dialog
            this.$alert(JSON.stringify(res.message), '商户新增失败！', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          });
        }).catch(() => {
          return
        });
      },

      /***
       * 提交网点内设备费率
      */
      Select_shop(id, mbmi_mid, restShow) {
        let show = this.addQueryList.every(res => {
          let bank_fee=res.bank_fee?res.bank_fee:0
          let mbmp_fee=res.mbmp_fee?res.mbmp_fee:0
          let mbpb_fee=res.mbpb_fee?res.mbpb_fee:0
          let number = bank_fee * 1 + mbmp_fee * 1 + mbpb_fee * 1
          return number < 30 || number == 30
        })
        if (!show) {
          this.$alert('平台服务费+银行服务费+渠道商服务费之和不可超过每笔交易额的30%', '处理失败', {
            confirmButtonText: '确定',
            callback: action => { }
          });
          return
        }
        console.log(this.addQueryList);
        this.addQueryList.forEach(res => {
          return res.bank_fee ? res.bank_fee : '0'
        })
        let obj = this.addQueryList
        let detail = obj.map(res => {
          console.log((100 - res.mbpb_fee * 1 - res.bank_fee * 1 - res.mbmp_fee * 1).toFixed(2) + "", '-0-0-0-0-0');
          return {
            bank_company_alias_id: this.currentCompanyId,
            device_uuid: res.di_uuid,
            mer_uuid: id,
            value: {
              mer_uuid: id,
              device_uuid: res.di_uuid,
              bank_uuid: this.customGlobal.userInfoExt.cui_station_id,
              platform_id: this.mid,//平台
              platform_fee: res.mbpb_fee ? res.mbpb_fee : "0",
              bank_id: this.mid,//银行-平台
              bank_fee: res.bank_fee ? res.bank_fee : "0",
              other1_id: res.bmp_mid ? res.bmp_mid : "",//渠道商
              other1_fee: res.mbmp_fee ? res.mbmp_fee : "0",
              mer_id: mbmi_mid,//商户-银联子商户号
              mer_fee: (100 - res.mbpb_fee * 1 - res.bank_fee * 1 - res.mbmp_fee * 1).toFixed(2) + ""
            }
          }
        })
        let _jsondata_ = {
          method: "2",
          detail: detail
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postBank13CompanyIdAndDeviceUnionConfig,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            loading.close();
            this.newlyAdded.dialogVisible = false
            this.serviceChargeMsg.dialogVisible = false
            console.log('res==', 11);
            if (restShow) {
              this.resetForm('ruleForm')
            }
            this.getData()
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">操作成功</span>'
            });
          } else {
            console.info("商户新增失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商1户新增失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户新增失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });

        /**
         * mbbb_bank_station_uuid  网点ID
           mbbb_mer_uuid  商户ID
           mbbb_device_uuid  设备ID
           mbbb_fee  银行费率
           mer_uuid
           device_uuid
         * */
        let _jsondata_1 = obj.map(res => {
          return {
            mbbb_bank_station_uuid: this.customGlobal.userInfoExt.cui_station_id,
            mbbb_mer_uuid: id,
            mbbb_device_uuid: res.di_uuid,
            mbbb_fee: res.bank_fee ? res.bank_fee : "0",
          }
        })
        var a2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Reset_equipment_and_rates_update,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_1) })
        );
        a2.then((res) => {

        })
      },

      /**
       * 模块弹窗
      */
      PopupControl(value, name, i) {
        console.log(value.row);
        name.dialogVisible = true
        name.name = value.row.mbmi_mer_name
        name.obj = value.row
        if (i) {
          switch (i) {
            case "功能开通":
              name.msg[0].radio = value.row.bank_mer_permissions.indexOf('1') > -1 ? "1" : "2"
              name.msg[1].radio = value.row.bank_mer_permissions.indexOf('2') > -1 ? "1" : "2"
              break;
            case "服务费管理":
              this.addQueryClick(name.obj.mbmi_uuid, this.addQueryList)
              break;
            case "商户信息":
              this.queryInformationClick();
              break;
            case "加盟网点查看":
              this.franchiseOutletsClick(name.obj.mbmi_uuid);
              break;
            default:
              break;
          }
        }
      },
      /**
       * 加盟网点信息查看
      */
      franchiseOutletsClick(id) {

        let that = this;
        let _jsondata_ = {
          perPageSize: this.franchiseOutlets.perPageSize,
          currentPage: this.franchiseOutlets.currentPage,
          bma_mer_uuid: id,
          bma_from_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          bma_from_bank_province_id: this.customGlobal.userInfoExt.cui_province_id,
          bma_from_bank_city_id: this.customGlobal.userInfoExt.cui_city_id,
          bma_from_bank_county_id: this.customGlobal.userInfoExt.cui_county_id,
          bma_from_bank_station_uuid: this.customGlobal.userInfoExt.cui_station_id,

        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.internal_merchant_affiliate_network_query,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.franchiseOutlets.setData = res.data.rows;
            that.franchiseOutlets.total = res.data.total
            loading.close();
          } else {
            // that.franchiseOutlets = [];
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.addQueryList = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.addQueryList = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      /**
       * 省市县选择器
      */
      selectPCAChange(data) {
        this.pca.province = data[0];
        this.pca.city = data[1];
        this.pca.area = data[2];
        console.info("选择省、市、县编码：" + JSON.stringify(this.pca));
        var loc = "";
        var loc_ext = ["全部", "全部", "全部"];
        for (let i = 0; i < this.ruleForm.selectedOptions.length; i++) {
          if (loc === "") {
            loc = CodeToText[this.ruleForm.selectedOptions[i]];
          } else {
            loc = loc + "," + CodeToText[this.ruleForm.selectedOptions[i]];
          }
          loc_ext[i] = CodeToText[this.ruleForm.selectedOptions[i]];
        }
        console.info("选择省、市、县中文：" + loc_ext);
        this.ruleForm.loc = loc_ext;
      },

      handleNodeClick(leafOnly, includeHalfChecked) {
        console.log(leafOnly, includeHalfChecked);
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => { });
      },
      handleChange(value) {
        console.log(value);
      },
      /**------分页-------start----*/
      adoptApprovedSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.adoptApproved.perPageSize = val;
        this.getData();
      },
      adoptApprovedCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.adoptApproved.currentPage = val;
        this.getData();
      },
      /**------分页-------start----*/
      handleSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.queryParam.pageSize = val;
        this.queryValveCurrentData();
      },
      handleCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.queryParam.currentPage = val;
        this.queryValveCurrentData();
      },
      /**------分页-------start----*/
      franchiseOutletsCurrentChange(val) {
        // console.log(`每页 ${val} 条`);
        this.franchiseOutlets.currentPage = val;
        this.franchiseOutletsClick(this.franchiseOutlets.obj.mbmi_uuid);
      },
      franchiseOutletsSizeChange(val) {
        // console.log(`当前页: ${val}`);
        this.franchiseOutlets.perPageSize = val;
        this.franchiseOutletsClick(this.franchiseOutlets.obj.mbmi_uuid);
      },
    },
  }
</script>
<style scoped>
  .transition-box {
    width: 200px;
    height: 100px;
    border-radius: 4px;
    background-color: #409EFF;
    text-align: center;
    color: #fff;
    padding: 40px 20px;
    box-sizing: border-box;
    margin-left: 20px;
  }

  .box {
    padding-right: 10px;
    width: 100%;
    height: 500px;
    overflow-y: scroll;
    overflow-x: hidden;
    table-layout: fixed;
    word-wrap: break-word;
    word-break: break-all;
  }

  .test-5::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 5px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }

  .test-5::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 10px;
    background-color: skyblue;
    background-image: -webkit-linear-gradient(45deg,
        rgba(255, 255, 255, 0.2) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.2) 50%,
        rgba(255, 255, 255, 0.2) 75%,
        transparent 75%,
        transparent);
  }

  .test-5::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    /* background   : ; */
    border-radius: 10px;
  }

  .account-formitem /deep/ .el-form-item {
    margin-bottom: 17px !important;
  }

  .account-plat-fixed-right /deep/ .el-table__fixed-right {
    height: 100% !important;
  }

  .account-plat-fixed /deep/ .el-table__fixed {
    height: 100% !important;
  }
</style>


